@import "../../../../libs/styles/main.scss";

.wrapper {
  display: flex;
  flex-direction: row;
  margin-bottom: $general-spacing-unit * 4;
  align-items: center;
}

.avatar {
  width: $general-spacing-unit * 9;
  height: $general-spacing-unit * 9;
  border-radius: 100%;
  margin-right: $general-spacing-unit * 2;
}

.message {
  font-size: 16px;
  display: block;
  color: $color-bali-hai;
  font-weight: 600;
}

.name {
  font-size: 24px;
  display: block;
  color: $color-cloud-burst;
  font-weight: 700;
}
