@import "../../libs/styles/main.scss";

.wrapper {
  background: $color-eastern-blue url("./banner-background.png") center bottom
    no-repeat;
  background-size: contain;
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: calc(15vh - 40px);
  box-sizing: border-box;

  @include breakpoint("mobile-and-tablet") {
    background-size: cover;
  }

  @include breakpoint("tablet") {
    background-size: 124% auto;
  }

  @include breakpoint("mobile") {
    padding-top: 20vw;
    background-position: -10vw bottom;
    background-size: 230% auto;
  }

  @include density("2x") {
    background-image: url("./banner-background@2x.png");
  }
}

.title {
  font-size: 6.4vh;
  line-height: 7vh;
  color: $color-white;
  text-align: center;
  margin: 0;

  @include breakpoint("mobile") {
    font-size: 70px;
    line-height: 76px;
  }

  @include breakpoint("mobile") {
    font-size: 7vw;
    line-height: 10vw;
    padding: 0;
  }

  i {
    font-style: normal;
    opacity: 0.7;
  }
}
