@import "../../libs/styles/main.scss";

.wrapper {
  text-align: center;
  padding-top: $general-spacing-unit * 11;
  margin-bottom: $general-spacing-unit * 11;

  @include breakpoint("mobile") {
    margin-top: $general-spacing-unit * 8;
    margin-bottom: $general-spacing-unit * 4;
    box-sizing: border-box;
    padding: 0 $mobile-side-spacing;
  }
}

.description {
  font-size: 20px;
  color: #253c45;
  text-align: center;
  line-height: 32px;
  max-width: $general-spacing-unit * 100;
  margin: 24px auto;

  @include breakpoint("mobile") {
    font-size: 18px;
    line-height: 28px;
  }
}
