@import "../../libs/styles/main.scss";

.wrapper {
  max-width: 1092px;
  margin: 0 auto;
  padding: $general-spacing-unit * 4 0;
  border-bottom: 1px solid rgba($color-bali-hai, 0.75);

  @include breakpoint("tablet") {
    width: calc(100vw - #{$general-spacing-unit * 18});
  }

  @include breakpoint("mobile") {
    width: calc(100vw - #{$general-spacing-unit * 14});
  }

  p {
    font-size: 24px;
    line-height: 34px;
    margin: $general-spacing-unit * 2 0;
    font-style: italic;
    text-align: center;

    @include breakpoint("mobile-and-tablet") {
      font-size: 18px;
      line-height: 28px;
    }
  }

  a {
    color: $color-eastern-blue;
    font-weight: bold;
  }
}
