@import "../../libs/styles/main.scss";

.wrapper {
  background: $color-swans-down;
  padding: $general-spacing-unit * 11 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: $general-spacing-unit * -5;

  a {
    width: calc(100% - #{$general-spacing-unit * 14});

    @include breakpoint("tablet-and-desktop") {
      width: 288px;
    }
  }

  @include breakpoint("mobile") {
    padding-top: $general-spacing-unit * 5;
    padding-bottom: $general-spacing-unit * 5;
  }
}

.title {
  font-size: 50px;
  line-height: 56px;
  margin-bottom: $general-spacing-unit * 4;
  color: $color-cloud-burst;
  text-align: center;

  @include breakpoint("mobile") {
    padding: 0 $general-spacing-unit * 9;
    font-size: 32px;
    line-height: 38px;
  }

  br {
    @include breakpoint("mobile") {
      display: none;
    }
  }

  i {
    color: $color-eastern-blue;
    font-style: normal;
  }
}
