@import "../../libs/styles/main.scss";

.wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  user-select: none;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;

  &:before {
    content: "";
    display: block;
    background: transparent url("./inkoin-icon.svg") 0 0 no-repeat;
    width: 26px;
    height: 26px;
    margin-right: $general-spacing-unit;
  }
}

.counter {
  font-size: 16px;
  line-height: 38px;
  color: $color-cloud-burst;
  font-weight: 700;
}
