@import "../../libs/styles/main.scss";

.wrapper {
  position: absolute;
  left: $general-spacing-unit * 6;
  top: $general-spacing-unit * 6;
  z-index: 1;

  @include breakpoint("tablet") {
    left: $general-spacing-unit * 10;
    top: 5vmin;
  }

  @include breakpoint("mobile") {
    left: $general-spacing-unit * 6;
    top: $general-spacing-unit * 5;
  }
}

.counter {
  font-size: 32px;
  line-height: 32px;
  color: $color_white;
  font-weight: 700;
  display: flex;
  flex-direction: row;
  align-items: center;

  @include breakpoint("tablet") {
    font-size: 4vmin;
    line-height: 4vmin;
  }

  @include breakpoint("mobile") {
    font-size: $general-spacing-unit * 3;
    line-height: $general-spacing-unit * 3;
  }

  &:before {
    content: "";
    display: block;
    background: transparent url("./inkoin-icon.svg") 0 0 no-repeat;
    width: 40px;
    height: 40px;
    background-size: contain;
    margin-right: $general-spacing-unit;

    @include breakpoint("tablet-and-desktop") {
      width: 4vmin;
      height: 4vmin;
    }

    @include breakpoint("mobile") {
      width: $general-spacing-unit * 3;
      height: $general-spacing-unit * 3;
    }
  }
}

.button {
  background: $color-surfie-green;
  color: $color-white;
  line-height: 44px;
  font-size: 16px;
  display: block;
  margin-top: $general-spacing-unit * 1.5;
  border-radius: 5px;
  padding: 0 $general-spacing-unit * 3;
  text-decoration: none;
  font-weight: 700;

  @include breakpoint("tablet-and-desktop") {
    line-height: 5vmin;
    font-size: 2vmin;
    padding: 0 3vmin;
  }

  @include breakpoint("mobile") {
    line-height: $general-spacing-unit * 4;
    font-size: 13px;
    padding: 0 $general-spacing-unit * 2;
  }
}
