@import "../../libs/styles/main.scss";

.wrapper {
  background: darken($color-catskill, 5%);
  width: 178px;
  border-radius: $general-spacing-unit;
  padding: $general-spacing-unit * 2;
  box-sizing: border-box;
  position: absolute;
  top: 40px;
  left: 10px;
  z-index: 1;
  opacity: 0;
  transform: translateX(-50%);
  transition: top ease 0.125s, opacity ease 0.125s, visibility ease 0.125s;
  visibility: hidden;
  outline-color: darken($color-catskill, 15%);

  &.visible {
    opacity: 1;
    visibility: visible;
    top: 50px;
  }

  &:before {
    content: "";
    width: $general-spacing-unit;
    height: $general-spacing-unit;
    background: darken($color-catskill, 5%);
    display: block;
    position: absolute;
    left: 50%;
    top: $general-spacing-unit * -0.5;
    transform: rotate(45deg);
  }
}

.paragraph {
  color: $color-cloud-burst;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  margin: 0 0 $general-spacing-unit;
}

.button {
  color: $color-eastern-blue;
  border: 0;
  padding: 0;
  background: transparent;
  text-align: center;
  display: block;
  font-family: $font-source-sans;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  width: 100%;
  outline-color: darken($color-catskill, 15%);
}
