@import "../../libs/styles/main.scss";

.wrapper {
  padding: $general-spacing-unit * 10 0;
  background-color: rgba($color-eastern-blue, 0.1);

  @include breakpoint("mobile") {
    padding: $general-spacing-unit * 5 $general-spacing-unit * 7;
  }
}

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 1092px;
  margin: 0 auto;
  box-sizing: border-box;

  @include breakpoint("tablet") {
    width: 100%;
    padding: 0 $general-spacing-unit * 8;
  }

  @include breakpoint("mobile") {
    flex-direction: column-reverse;
    width: auto;
  }
}

.textWrapper {
  @include breakpoint("tablet") {
    width: 288px;
  }

  @include breakpoint("mobile") {
    width: 100%;
  }
}

.title {
  font-size: 56px;
  line-height: 64px;
  color: $color-cloud-burst;
  margin: 0 0 $general-spacing-unit * 2;

  span {
    color: $color-eastern-blue;
  }

  @include breakpoint("mobile-and-tablet") {
    font-size: 32px;
    line-height: 38px;
  }

  @include breakpoint("mobile") {
    text-align: center;
  }
}

.description {
  font-size: 24px;
  line-height: 34px;
  color: $color-cloud-burst;
  margin: 0 0 $general-spacing-unit * 3;

  @include breakpoint("mobile-and-tablet") {
    font-size: 18px;
    line-height: 28px;
  }

  @include breakpoint("mobile") {
    text-align: center;
  }
}

.button {
  background-color: $color-eastern-blue;
  color: $color-white;
  border: 0;
  height: 56px;
  line-height: 56px;
  font-size: 18px;
  font-family: $font-source-sans;
  font-weight: 700;
  cursor: pointer;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color ease 0.15s;
  width: 100%;
  max-width: 350px;
  text-decoration: none;

  img,
  svg {
    margin-right: $general-spacing-unit;
  }

  &:hover {
    background: darken($color-eastern-blue, 7.5%);
  }

  @include breakpoint("mobile") {
    max-width: none;
  }
}

.image {
  margin-left: $general-spacing-unit * 10;
  pointer-events: none;

  @include breakpoint("tablet") {
    margin-left: $general-spacing-unit * 3;
    width: calc(50vw - #{$general-spacing-unit * 7});
  }

  @include breakpoint("mobile") {
    margin-left: 0;
    margin-bottom: $general-spacing-unit * 4;
    width: 100%;
  }
}
